import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    /*底部显示状态*/
    fadeIn: true,
    /*顶部定位状态管理*/
    headerIndex: false,
    /*顶部样式状态管理*/
    headerSpecial: false,
    /*底部样式状态管理*/
    footerColor: false,
    /*设备宽度*/
    device:true
  },
  mutations: {
    change (state, payload){
      for (let val of payload){
        state[val] = !state[val]
      }
    },
    changeDeviceTrue: (state) => {
      return state.device = true;
    },
    changeDeviceFalse: (state) => {
      return state.device = false;
    }
  },
  actions: {
  },
})

export default store



