<template>
  <div class="about-wrap">
    <section class="in-banner">
      <img v-bind:src="banner">
    </section>
    <section class="main-wrap">
      <div class="sec-one sec">
        <p class="m-title">关于我们</p>
        <div class="content" v-html="about.text"></div>
      </div>
      <div class="sec-two sec">
        <p class="m-title">联系我们</p>
        <div class="content">
          <ul>
            <li v-for="info in infos">
              {{info.title}} : {{info.text}}
            </li>
          </ul>
        </div>
      </div>
      <div class="sec-two sec">
        <div id="map"></div>
      </div>
    </section>
  </div>
</template>

<script>
  /******************/
  /******************/
  //region    //import

  import axios from "axios";
  //endregion

  /******************/
  /******************/
  //region    //initialize


  //endregion

  /******************/
  /******************/
  //region    //export

  export default {
    name:"about",
    data() {
      return {
        about:[],
        infos:[],
        map:[],
        banner:"../../static/images/aboutus_banner.jpg"
      }
    },
    mounted() {
      axios.get("static/json/about.json").then(res => {
        let {about, info, map} = res.data;
        this.about = about;
        this.infos = info;
        this.map = map;
        let maps = new BMap.Map("map");
        let point = new BMap.Point(this.map.x,this.map.y);
        maps.centerAndZoom(point, 12);
        var marker = new BMap.Marker(point);// 创建标注
        maps.addOverlay(marker);
      });
    },
    methods: {},

  }

  //endregion

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .main-wrap{
    margin-top: 5vw;
  }
  .m-title{
    font-size: 2.8rem;
    color: #4d4d4d;
    margin-bottom: 3vw;
  }
  .sec{
    margin-bottom: 3vw;
    .content{
      color: #4d4d4d;
      font-size: 1.8rem;
      line-height: 2;
    }
  }
  .sec-one .content{
    text-indent: 38px;
  }
  #map{
    width: 100%;
    height: 60vh;
  }
</style>



