<template>
  <el-container>
    <el-header></el-header>
    <el-main class="main-wrap wrap">
      <p class="title">{{ title }}</p>
      <p class="info">{{ date }} {{ source }}</p>
      <div v-html="text" class="content"></div>
    </el-main>
  </el-container>
</template>

<script>
  /******************/
  /******************/
  //region    //import
  import axios from "axios";

  //endregion

  /******************/
  /******************/
  //region    //initialize


  //endregion

  /******************/
  /******************/
  //region    //export

  export default {
    name: "newsdetail",
    data() {
      return {
        title: "",
        date: "",
        author: "",
        source: "",
        text: "",
        path:""
      }
    },
    mounted,
    destroyed,
    methods: {}

  }
  //endregion

  /******************/
  /******************/
  //region    //function

  /**
   *组件挂载成功执行函数
   */
  function mounted() {
    this.$store.commit('change', ["fadeIn", "headerSpecial"]);
    this.path = this.$route.params.id ;
    axios.get(`/static/json/news/${this.path}.json`).then(res => {
      let {title, date, author, source, text} = res.data;
      this.title = title;
      this.date = date;
      this.author = author;
      this.source = source;
      this.text = text;
    });
  }

  function destroyed() {
    this.$store.commit('change', ["fadeIn", "headerSpecial"]);
  }
  //endregion

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .wrap{
    text-align: center;
    .title{
      font-size: 28px;
      margin-bottom: 17px;
    }
    .info{
      color:#808080;
      font-size: 12px;
      padding-bottom: 28px;
      border-bottom: 1px #ccc solid;
    }
    .content{
      text-indent: 34px;
      text-align: left;
      padding-top: 44px;
      font-size: 12px;line-height: 2;
      color: #4d4d4d;
    }
  }
</style>



