<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>6
export default {
  name: 'app',
  components: {},
  mounted () {
    this.handleResize();
    window.addEventListener('resize', this.handleResize)
  },
  methods: {
    handleResize (){
      let nwidth = window.innerWidth;
      if(nwidth&&(nwidth<960)){
        this.$store.commit("changeDeviceFalse");
      }
      else{
        this.$store.commit("changeDeviceTrue");
      }
    }
  }
}
</script>

<style>

  /*app宽度*/
  #app{
    width: 100%;overflow: hidden;
  }

  /**
   * 公用样式
   * @authors Derry
   * @date    2017-10-13 09:26:33
   * @version 1.0.0
   */
  /*初始化样式*/
  *,*:before,*:after{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
  }
  /*自定义字体*/
  @font-face {
    font-family:'iconfont';
    src:url("assets/font/medium.ttf") format('truetype');
  }
  ul,li{
    list-style: none;
  }
  a{
    text-decoration: none;
    color: inherit;
  }
  html, body{
    font-size: 10px;
  }
  body{
    font-family:iconfont;
    /*, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;*/
    color: #444;
    /*background: #ddd;*/
  }
  /*reset rem*/
  @media screen and (min-width: 1500px) {
    html, body{
      font-size: 10px;
    }
  }
  img{
    max-width: 100%;
  }


  /*清除浮动*/
  .clearFix:before,.clearFix:after{
    content: "";
    display: table;
  }
  .clearFix:after{
    clear: both;
  }
  .fl{
    float: left;
  }
  .fr{
    float: right;
  }

  /*居中布局*/
  .main-wrap{
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
  }
  @media screen and (max-width: 960px) {
    html, body{
      font-size: 5px;
    }
  }

</style>



