<template>
  <p class="copyright" v-if="fadeIn"  v-bind:class="{ copyrightIndex: footerColor }">{{ copyright.text }}</p>
</template>

<script>
  /******************/
  /******************/
  //region    //import
  import axios from "axios";
  import { mapState } from 'vuex'

  //endregion

  /******************/
  /******************/
  //region    //initialize


  //endregion

  /******************/
  /******************/
  //region    //export

  export default {
    name : 'footers',
    data() {
      return {
        copyright:""
      }
    },
    mounted,
    methods: {},
    computed: {
      ...mapState(["fadeIn", "footerColor"])
    },
  }
  //endregion

  /******************/
  /******************/
  //region    //function

  /**
   *组件挂载成功执行函数
   */
  function mounted() {
    axios.get("/static/json/about.json").then(res => {
      let { copyright} = res.data;
      this.copyright = copyright;
    });
  }

  //endregion

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
 .copyright{
   text-align: center;
   color: #999;
   font-size: 1.4rem;
   padding: 3rem 0;
   line-height: 1.2;
   background-color: #f2f2f2;
 }
 .copyrightIndex{
   color: #fff;
   background-color: #010114
 }

</style>



