import Vue from 'vue'
import Router from 'vue-router'

/****************/
//region  //import components
import mains from '@/components/globalcompoent/main'
import about from '@/components/about'
import news from '@/components/news'
import product from '@/components/product'
import home from '@/components/home'
import recruit from '@/components/recruit'
import newsdetail from '@/components/news/NewsDetail'
//endregion


Vue.use(Router)


const routerMap = [
  {
    path: '/',
    name: 'mains',
    redirect: '/home',
    component: mains,
    children:[
      {
        path: '/about',
        name: 'about',
        component: about
      },
      {
        path: '/news',
        name: 'news',
        component: news,
      },
      {
        path: '/news/:id',
        name: 'newsdetail',
        component: newsdetail,
      },
      {
        path: '/product/:id',
        name: 'product',
        component: product
      },
      {
        path: '/home',
        name: 'home',
        component: home
      },
      {
        path: '/recruit',
        name: 'recruit',
        component: recruit
      }
    ]
  }
];

export default new Router({
  routes: routerMap,
  mode: 'history',
})



