<template>
  <div class="news-wrap">
    <div class="banner">
      <img v-bind:src="banner">
    </div>
    <div class="news-list">
      <div class="news clearFix" v-for="news in views">
        <div class="t">
          <router-link :to="{
          path:`/news/${news.name}`,
          }"><img v-bind:src="news.path"></router-link>
        </div>
        <div class="c">
          <div class="c-title">
            <router-link :to="{
          path:`/news/${news.name}`,
          }">{{news.title}}</router-link>
          </div>
          <div class="c-content">
            {{news.text}}
          </div>
        </div>
        <div class="other">
          <div class="date">05-22</div>
          <div class="year">2018</div>
          <div class="link">
            <router-link :to="{
          path:`/news/${news.name}`,
          }"></router-link>
          </div>
        </div>
      </div>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="count" :page-size="pageSize"
      @prev-click="prevPage" @next-click="nextPage" @current-change="changePage">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  /******************/
  /******************/
  //region    //import
  import axios from "axios";

  //endregion

  /******************/
  /******************/
  //region    //initialize


  //endregion

  /******************/
  /******************/
  //region    //export

  export default {
    name: "news",
    data() {
      return {
        newes : [],
        views: [],
        pageSize:6,
        currentPage:0,
        banner: "../../static/images/news_banner.jpg"
      }
    },
    mounted,
    methods: {
      nextPage:function() {
        let page = this.currentPage;
        page = page + 1;
        this.currentPage= page ;
        let start = this.pageSize* (page-1);
        let end = this.pageSize* page;
        this.views = this.newes.slice(start, end);
      },
      prevPage: function () {
        let page = this.currentPage;
        page = page - 1;
        this.currentPage= page;
        let start = this.pageSize* (page-1);
        let end = this.pageSize* page;
        this.views = this.newes.slice(start, end);
      },
      changePage:function(val) {
        this.currentPage= val ;
        let page = this.currentPage;
        let start = this.pageSize* (page-1);
        let end = this.pageSize* page;
        this.views = this.newes.slice(start, end);
      },
    },
    computed: {
      count (){
        return this.newes.length;
      }
    }

  }
  //endregion

  /******************/
  /******************/
  //region    //function

  /**
   *组件挂载成功执行函数
   */
  function mounted() {
    axios.get("static/json/news/news.json").then(res => {
      let news = res.data.news;
      this.newes = news;
      this.views = news.slice(0, this.pageSize)
    });
  }

  //endregion

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .banner{
    margin-bottom: 100px;
  }
 .news{
   width:100%;height: 200px;overflow: hidden;
   margin:0 auto;
   margin-bottom: 38px;
   max-width: 1200px;
   background: #f7f7f7;
   .t{
     width: 290px;
     height: 200px;
     padding: 15px;
     margin-right: 80px;
     float: left;
     a{
       display: block;
       width: 100%;
       height: 100%;
     }
   }
   .c{
     float: left;
     transition: margin .5s;
     padding: 45px 0 40px;
     max-width: 440px;
     .c-title{
       font-size: 18px;
       color:#4d4d4d;
       margin-bottom: 30px;
     }
     .c-content{
       font-size: 14px;
       line-height: 24px;
       color: #808080;
     }
   }
   .other{
     float: right;
     padding:  50px 0 40px;
     margin-right: 80px;
     transition: all .5s;
     .date{
       font-size: 24px;line-height: 28px;
       color: #4d4d4d;
     }
     .year{
       font-size: 14px;line-height: 24px;
       color: #4d4d4d;
       margin-bottom: 38px;
     }
     .link{
       width: 30px;
       height: 30px;
       background-image: url("../../assets/but_outline.png");
       a{
         display: block;width: 100%;height: 100%;
       }
     }
   }
   &:hover{
     box-shadow: 5px 5px 10px rgba(6,5,57,0.2);
     .t{
       background: #808080;
     }
     .c{
       margin-left: 15px;
     }
     .other{
       margin-right: 100px;
       .link{
         background-image: url("../../assets/but.png");
         float: right;
       }
     }
   }
 }
  .el-pagination{
    text-align: center;
    margin-bottom: 8rem;
  }
</style>



