<template>
  <div class="header clearFix" v-bind:class="{headerSpecial:headerSpecial, headerIndex:headerIndex}">
    <div class="main-wrap main">
      <section class="logo">
        <router-link to="/"><img v-bind:src="logo"></router-link>
      </section>
      <nav>
        <ul class="clearFix">
          <li v-for="nav in navs">
            <router-link :to="nav.path">{{nav.name}}</router-link>
          </li>
        </ul>
      </nav>
    </div>
  </div>
</template>

<script>
  /******************/
  /******************/
  //region    //import
  import { mapState } from 'vuex'

  //endregion

  /******************/
  /******************/
  //region    //initialize


  //endregion

  /******************/
  /******************/
  //region    //export

  export default {
    name :'headers',
    data() {
      return {
        logos:{
          logo: "../../static/images/logo.png",
          h_Logo: "../../static/images/h_Logo.png"
        },
        navs : [
          {
            name: "关于我们",
            path: "/about"
          },
          {
            name: "新闻动态",
            path: "/news"
          },
          {
            name: "产品与解决方案",
            path: ""
          },
          {
            name: "首页",
            path: "/home"
          }
        ]
      }
    },
    mounted,
    destroyed,
    computed: {
      logo () {
        if(this.$store.state.headerSpecial||this.$store.state.headerIndex) {
          return this.logos.logo;
        }
        else{
          return this.logos.h_Logo;
        }
      },
      ...mapState(["headerSpecial", "headerIndex"])
    },
    methods: {},

  }
  //endregion

  /******************/
  /******************/
  //region    //function

  /**
   *组件挂载成功执行函数
   */
  function mounted() {

  }
  function destroyed() {

  }
  //endregion

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .main{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .5rem 0;
  }
  .header{
    width: 100%;
    position: relative;
    .logoIndex{
      display: none;
    }
    nav{
      li{
        float: right;
        font-size: 1.4rem;
        padding: 5px 10px;
        margin:0 10px;
        a{
          color: inherit;
          display: block;
          width: 100%;
          height: 100%;
        }
        &:hover{
          color:#4353ff;
          border: 1px #4353ff solid;
          padding: 4px 9px
        }
      }
    }
  }
  .headerIndex{
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    color: #fff;
  }
  .headerSpecial{
    background: #010115;
    nav li{
      color: #fff;
    }
  }
</style>



