<template>
  <div>
    <div class="banner">
      <img v-bind:src="banner">
    </div>
    <div>
      <section class="sec-one sec">
        <div class="main-wrap flex-wrap">
          <div v-for="service in services" class="service">
            <div class="t">
              <img v-bind:src="service.path">
            </div>
            <p class="title">
              {{ service.title }}
            </p>
            <div class="content">
              {{ service.content }}
            </div>
          </div>
        </div>
      </section>
      <section class="sec-two sec">
        <div class="sec-title">产品特点</div>
        <div class="main-wrap">
          <div class="features flex-wrap" v-if="device">
            <div class="feature" v-for="feature in features">
              <div class="photo">
                <img v-bind:src="feature.path">
              </div>
              <div class="title" v-html="feature.title"></div>
              <div class="content" v-html="feature.content"></div>
            </div>
          </div>
          <el-carousel indicator-position="outside" v-else style="flex-basis: 100%">
            <el-carousel-item v-for="feature in features" :key="feature.title" class="sm-feature">
              <div class="photo">
                <img v-bind:src="feature.path">
              </div>
              <div class="title" v-html="feature.title"></div>
              <div class="content" v-html="feature.content"></div>
            </el-carousel-item>
          </el-carousel>
        </div>
      </section>
      <section class="sec-three sec">
        <div class="sec-title">产品与解决方案</div>
        <div class="main-wrap flex-wrap">
          <div class="product" v-for="product in products">
            <div class="t">
              <img v-bind:src="product.img">
            </div>
            <div class="c flex-wrap">
              <div class="title">
                <div class="c-title">{{ product.mtitle }}</div>
                <div class="c-content">{{ product.htitle }}</div>
              </div>
              <div class="router">
                <router-link :to="{
          path:`/product/${product.path}`,
          }"><img src="../../assets/but_outline.png">
                </router-link>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section class="sec-four sec">
        <div class="sec-title">公司动态</div>
        <div class="main-wrap">
          <div class="dynamic" v-for="dynamic in dynamics">
            <div class="borderline">
              <div class="circle"></div>
              <div class="line"></div>
            </div>
            <div class="content">
              <div class="content-text">
                <p class="date">{{ dynamic.date }}</p>
                <p class="event">{{ dynamic.event }}</p>
              </div>
              <div class="baseline"></div>
            </div>
          </div>
        </div>
        <div class="router">
          <button>
            <span>查看更多</span>
            <i class="iconfont icon-arrowright"></i>
          </button>
        </div>
      </section>
      <section class="sec-five sec">
        <div class="sec-title">合作伙伴</div>
        <div class="main-wrap flex-wrap">
          <div class="friend" v-for="friend in friends">
            <img v-bind:src="friend.logo">
          </div>
        </div>
      </section>
      <section class="sec-six sec">
        <div class="main-wrap flex-wrap">
          <div class="nav" v-for="nav in navs">
            <p class="menu-item">{{ nav.name }}</p>
            <div class="sub-menu" v-for="subnav in nav.subnav">
              <p class="sub-menu-item">
                <router-link :to="{
                path:`${subnav.path}`}">
                  {{ subnav.name }}
                </router-link>
              </p>
            </div>
          </div>
          <div class="contact">
            <div class="contact-item">
              <i class="iconfont icon-location"></i>&nbsp;&nbsp;&nbsp;&nbsp;
              <p>{{ contacts.phone }}</p>
            </div>
            <div class="contact-item">
              <i class="iconfont icon-email"></i>&nbsp;&nbsp;&nbsp;&nbsp;
              <p>{{ contacts.email }}</p>
            </div>
            <div class="contact-item">
              <i class="iconfont icon-phone"></i>&nbsp;&nbsp;&nbsp;&nbsp;
              <p>{{ contacts.position }}</p>
            </div>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
  import axios from "axios";

  export default {
    name: "home",
    data() {
      return {
        banner:"../../../static/images/home/banner.jpg",
        services:[],
        features:[],
        products:[],
        dynamics:[],
        friends:[],
        navs:[
          {
            name:"首页",
          },
          {
            name:"产品与解决方案",
            subnav:[
              {
                name:"P2P网络借贷监管",
                path:"/product/product01"
              },
              {
                name:"金融企业备案",
                path:"/product/product04"
              },
              {
                name:"公开披露监管",
                path:"/product/product05"
              },
              {
                name:"金融违规举报",
                path:"/product/product03"
              },
              {
                name:"小额借贷监管",
                path:"/product/product02"
              },
              {
                name:"征信信息对接查询",
                path:"/product/product06"
              }
            ]
          },
          {
            name:"新闻动态"
          },
          {
            name:"关于我们",
            subnav:[
              {
                name:"企业简介",
                path:""
              },
              {
                name:"联系我们",
                path:""
              },
              {
                name:"商务合作",
                path:""
              },
              {
                name:"招聘信息",
                path:"/recruit"
              }
            ]
          },
        ],
        contacts:{
          phone:"0991-5840000",
          email:"lianxin@lxwangluo.com",
          position:"四川省成都市高新区天府三街香年广场T2 2502"
        },
      }
    },
    mounted,
    destroyed,
    computed:{
      device () {
        return this.$store.state.device;
      }
    }
  }

  function mounted() {
    this.$store.commit('change', ["headerIndex", "footerColor"]);
    axios.get("static/json/index.json").then(res => {
      let {features, dynamics, friends, services} = res.data;
      this.features = features;
      this.dynamics = dynamics;
      this.friends = friends;
      this.services = services;
      axios.get("static/json/product/product.json").then(res => {
        let products = res.data.products;
        this.products = products
      });
    });
  }

  function destroyed() {
    this.$store.commit('change', ["headerIndex", "footerColor"]);
  }
</script>

<style scoped lang="scss">
  .sec{
    padding: 4vw 0;
  }
  .sec-title{
    margin-bottom:4vw;
    font-size: 2.8rem;
    color: #fff;
    letter-spacing: 3px;
    text-align: center;
  }
  .flex-wrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
  }
  .sec-one {
     padding: 55px 0 115px;
     .main-wrap{
       padding: 0 20px;
       text-align: center;
       .service{
         max-width: 300px;
         margin-bottom: 20px;
         .title{
           font-size: 20px;
           color:#333333;
           padding: 40px 0 50px;
         }
         .content{
           font-size: 14px;
           color: #4d4d4d;
           line-height: 1.8;
         }
       }
     }
   }
  .sec-two{
    background: url("../../../static/images/home/home_tese_bg2.jpg") no-repeat center center;
    .main-wrap{
      text-align: center;
      color: #fff;
      .features{
        flex-wrap: nowrap;
      }
      .feature{
        position: relative;
        transition: all 1.5s;
        top: 0;
        width: 356px;
        height: 676px;
        background: url("../../../static/images/home/home_tese_bg.png") no-repeat center center;
        display: flex;
        flex-direction: column;
        padding: 3.5rem;
        &:hover{
          top: -25px;
        }
        .photo{
          padding: 50px 0;
        }
        .title{
          font-size: 20px;
          margin-bottom: 4rem;
        }
        .content{
          line-height:2;
          font-size: 1.3rem;
          text-align: left;
        }
      }
      .sm-feature{
        .title{
          font-size: 1.6rem;
          padding: 1.6rem 0;
        }
        .content{
          padding:0 10%;
        }
      }
    }
  }
  .sec-three{
    background: #f5f5f5;
    .sec-title{
      color: #4d4d4d;
    }
    .product{
      margin-bottom: 4vw;
      position: relative;
      overflow: hidden;
      top: 0;
      transition: top 1s;
      .t{
        overflow: hidden;
        img{
          transition: all 1s;
        }
      }
      .c{
        justify-content: space-between;
        padding: 1vw 2vw;
        .title{
          float: left;
          .c-title{
            font-size: 1.8rem;
            line-height: 2;
            color: #4d4d4d;
          }
          .c-content{
            font-size: 1.4rem;
            color: #808080;
          }
        }
        .router{
          float: right;
          visibility: hidden;
        }
      }
      &:hover{
        top: -15px;
        background: #fff;
        box-shadow: 0px 6px 10px 0px rgba(6, 5, 57, 0.2);
        .router{
          visibility: visible;
        }
        .t img{
          transform: scale(1.1);
        }
      }
    }
  }
  .sec-four{
    background: url("../../../static/images/home/home_dongtai_bg.jpg") no-repeat center center;
    color: #fff;
    padding: 6rem 0;
    .main-wrap{
      margin-bottom: 4rem;
    }
    .dynamic{
      padding:2rem 3.5rem;
      position: relative;
      .borderline{
        position: absolute;
        top: 35px;
        bottom: -35px;
        width: 0px;
        .line{
          border: 1px #ddd solid;height: calc( 100% - 16px);
        }
        .circle{
          width: 16px;
          height: 16px;
          border-radius: 16px;
          border: 1px #ddd solid;
          position: relative;
          right: 7px;
        }
      }
      .content{
        .content-text{
          position: relative;
          top: 0;left: 0;
          right: 0;
          transition: all 2s;
          .date{
            font-size: 2.4rem;
            margin-bottom: 1rem;
          }
          .event{
            font-size: 1.4rem;
            margin-bottom: 1rem;
          }
        }
        .baseline{
          width: 10%;
          line-height: 0;
          transition: width 2s;
          border-bottom: 2px #ddd solid;
          display: inline-block;
        }
      }
      &:nth-child(2n+1){
        text-align: left;
        margin-left: 50%;
        .borderline{
          left: -1px;
        }

      }
      &:nth-child(2n){
        text-align: right;
        margin-right: 50%;
        .borderline{
          right: 1px;
          .circle{
            background: #ddd;
          }
        }
      }
      &:last-child{
        .borderline{
          .line{
            height: 0;
            border: none;
          }
        }
      }
      &:hover{
        .baseline{
          width: 70%;
        }
        .content-text{
          top: -15px;
        }
        &:nth-child(2n+1){
          .content-text{
            left: 15px;
          }
        }
        &:nth-child(2n){
          .content-text{
            left: -15px;
          }
        }
      }
    }
    .router{
      display: flex;
      justify-content: space-around;
      button{
        width: 160px;
        height: 40px;
        background: none;
        border:1px #ddd solid;
        color: #fff;
        span,i{
          position: relative;
          left: 0;
          transition: left 1s;
        }
        &:hover{
          span{
            left: -10px;
          }
          i{
            left: 10px;
          }
        }
      }
    }
  }
  .sec-five{
    .sec-title{
      color: #4e4e4e;
    }
    .friend{
      flex-basis: 25%;
      padding: 2rem;
    }
  }
  .sec-six{
    color: #fff;
    font-size:1.6rem ;
    background-image: linear-gradient(180deg, #0b1b44 0%, #010114 100%);
    .flex-wrap{
      align-items: flex-start;
    }
    .menu-item{
      font-size:2.4rem ;
      margin-bottom: 2rem;
    }
    .sub-menu{
      line-height: 3;
      &:hover{
        color: #409eff;
      }
    }
    .nav{
      align-self:flex-start;
    }
    .contact-item{
      display: flex;
      line-height: 1.8;
      margin-bottom: 2rem;
      align-items: center;
      i{
        color: #409eff;
        font-size: 2rem;
      }
    }
  }
</style>



