<template>
  <div class="recruit-wrap">
    <div class="banner">
      <img v-bind:src="banner">
    </div>
    <div class="main-wrap">
      <div class="p-name">{{name}}</div>
      <div class="recruit-list">
        <div class="recruit" v-for="recruit in recruits">
          <div class="recruit-info main-info">
            <p class="title">{{ recruit.title }}</p>
            <div class="content" v-html="recruit.gwzz"></div>
          </div>
          <div class="recruit-info">
            <p class="title">岗位职责</p>
            <div class="content" v-html="recruit.gwzz"></div>
          </div>
          <div class="recruit-info">
            <p class="title">任职要求</p>
            <div class="content" v-html="recruit.gwyq"></div>
          </div>
          <div class="recruit-info">
            <p class="title">具有以下经验者有限</p>
            <div class="content" v-html="recruit.rzyq"></div>
          </div>
        </div>
    </div>
    </div>
  </div>
</template>

<script>
  /******************/
  /******************/
  //region    //import
  import axios from "axios";

  //endregion

  /******************/
  /******************/
  //region    //initialize


  //endregion

  /******************/
  /******************/
  //region    //export

  export default {
    data() {
      return {
        banner: "../../static/images/recruitment_banner.jpg",
        name: "招聘信息",
        recruits:[]
      }
    },
    mounted,
    methods: {},

  }
  //endregion

  /******************/
  /******************/
  //region    //function

  /**
   *组件挂载成功执行函数
   */
  function mounted() {
    axios.get("/static/json/recruit.json").then(res => {
      let { recruits } = res.data;
      this.recruits = recruits;
    });
  }

  //endregion

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .p-name{
    font-size: 28px;
    color: #4d4d4d;
    margin-bottom: 30px;
  }
  .recruit-list{
    display: flex;
    justify-content: space-between;
  }
  .recruit{
    width: 570px;
    .main-info{
      .title{
        height: 12px;background: rgba(67,83,255,0.54);
        display: inline-block;padding: 0 5px;
        line-height:.01!important;
        margin-bottom: 25px!important;
      }
    }
    .recruit-info{
      padding: 20px 0 30px;
      border-bottom: 1px #ddd solid;
      .title{
        font-size: 16px;line-height: 16px;color: #404040;
        margin-bottom: 35px;
      }
      .content{
        font-size: 14px;line-height: 32px;
        color: #808080;
      }
    }
  }
</style>



