<template>
  <div class="product-detail main-wrap">
    <section class="title">
      <p class="m-title">{{ mtitle }}</p>
      <p class="h-title">{{ htitle }}</p>
    </section>
    <article class="content">
      <p class="p-title">行业现状:</p>
      <div class="p-content" v-html="hyxz"></div>
    </article>
    <article class="content">
      <p class="p-title">解决方案:</p>
      <div class="p-content" v-html="solution"></div>
    </article>
  </div>
</template>

<script>
  /******************/
  /******************/
  //region    //import
  import axios from "axios";

  //endregion

  /******************/
  /******************/
  //region    //initialize


  //endregion

  /******************/
  /******************/
  //region    //export

  export default {
    name: "product",
    data() {
      return {
        mtitle: "",
        htitle: "",
        hyxz: "",
        solution: ""
      }
    },
    mounted,
    destroyed,
    methods: {},

  }
  //endregion

  /******************/
  /******************/
  //region    //function

  /**
   *组件挂载成功执行函数
   */
  function mounted() {
    this.$store.commit('change',['fadeIn', 'headerSpecial']);
    this.path = this.$route.params.id ;
    axios.get(`/static/json/product/${this.path}.json`).then(res => {
      let {mtitle, htitle, hyxz, solution} = res.data;
      this.mtitle = mtitle;
      this.htitle = htitle;
      this.hyxz = hyxz;
      this.solution = solution
    });
  }

  function destroyed() {
    this.$store.commit('change',['fadeIn', 'headerSpecial']);
  }

  //endregion

</script>
flex
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.product-detail{
  .title{
    padding: 2.4rem 0;
    border-bottom: 1px #ddd solid;
    .m-title{
      color: #4d4d4d;
      font-size: 2.8rem;
      margin-bottom: 1.2rem;
    }
    .h-title{
      color: #808080;
      font-size: 1.2rem;
    }
  }
  .content{
    padding: 2.2rem 0;
    color: #4d4d4d;
    .p-title{
      font-size: 1.6rem;
      margin-bottom: 2rem;
    }
    .p-content{
      font-size: 1.2rem;line-height: 2;
      padding-left: 2rem;
    }
  }
}
</style>



